// Center block
@mixin center-block {
	display: block;
	margin-left: auto;
	margin-right: auto;
}
//mixin for tablet breakpoint
@mixin tablet {
    @media (min-width: $tablet-width) {
      @content;
    }
  }
  
//mixin for desktop breakpoint
@mixin desktop {
  @media (min-width: $desktop-width) {
    @content;
  }
}


